import Vue from 'vue'
import Vuex from 'vuex'
// import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex)


const store = new Vuex.Store({
  state: {
    answer: {},
    isConfirmed: {
      JDQYSDS: false, // 季度企业所得税
      NDQYSDS: false, // 年度企业所得税
      ZZS: false,     // 增值税
      CWBB: false,    // 财务报表
      CJS: false,     // 城建税申报
      YHS: false,     // 印花税
      taxes_status: '按次申报' //印花税纳税期限
    }, // 是否已经申报
    tableData: {},      //表内数据保存
    companyInform: {},   //企业信息保存
    companyCurrent: null   //企业预设数据信息
  },
  //持久化插件使用
  // plugins: [createPersistedState({
  //   storage: window.sessionStorage,
  // })],
  mutations: {
    resetAnswer(state) {
      state.answer = {}
    },
    setConfirm(state, data) {
      state.isConfirmed[data.key] = data.value
      // 保存答案
      state.answer['confirmed_' + data.key] = data.value
    },
    // 保存数据
    saveData: function (state, data) {
      state.tableData[data.name] = data.data
      // 保存答案
      state.answer['table_' + data.name] = data.data
    },
    saveConfirm(state, data) {
      state.isConfirmed[data.name] = data.data

      // 保存答案
      state.answer['confirmed_' + data.name] = data.data
    },
    saveComCurrent(state, data) {
      state.companyCurrent = data
    }
  }
})

export default store




