    methods: {
      sendMessage(cmd, params) {
        window.parent.postMessage({ cmd, params }, '*')
      },
      handleMessage(msg) {
        const { data } = msg
        if (data.cmd === 'preset') { // 预设的企业信息
          this.current.gscompany = {
            trade_id: data.params['id'],
            firm_name: data.params['company_title'],
            firm_code: data.params['tax_id'],
            firm_trade: data.params['industry'],
            firm_detail_trade: '生产企业',  //企业类型
            firm_registration_trade: '有限责任公司', // 企业注册形式
            firm_legal_person: data.params['legal_person'],
            firm_legal_person_id: data.params['legal_person_id'],
            firm_address: data.params['register_address'],
            firm_operating: data.params['register_address'],
            firm_bank_code: data.params['bank_id'],
            firm_type: '内资企业',
            firm_telephone: data.params['current_user_tel'],
            month: data.params['month'],
            telephone_name: data.params['current_user'],
            taxation_name: data.params['current_user'],  // 当前的登录人员姓名
            agent_id: '0',
            tax_type: data.params['tax_type'] // 纳税类型 1一般纳税人 2 小规模
          }
          let arr = [] // 用于储存拆借出来的元素
          J.each(this.current.content, function (k, v) {
            let attr = k.split('-')
            attr.push(v)
            arr.push(attr)
          })
          let art = {}
          for (let i = 0; i < arr.length; i++) {
            if (arr[i].length === 3) {
              if (!art[arr[i][0]]) {
                art[arr[i][0]] = {}
              }
              art[arr[i][0]][arr[i][1]] = arr[i][2]
            }
          }
          this.current.content = art
          this.$store.commit('saveComCurrent', this.current)

        }
        if (data.cmd === 'getAnswer') {
          const answer = this.$store.state.answer
          let keys = Object.keys(answer)
          if (keys.length > 0) {
            this.sendMessage('echo', { business_id: data.params['business_id'], answer: answer })
            this.$store.commit('resetAnswer')
          }
        }
      }
    },
    mounted() {
      this.$nextTick(() => {
        console.log('国税系统注册消息成功')
        window.addEventListener('message', this.handleMessage)
      })
    },
    destroyed() {
      window.removeEventListener('message', this.handleMessage)
