          if (this.$store.state.companyCurrent.gscompany.tax_type === 2) {  // 小规模
            this.funBar = this.vatPer
          } else {  // 一般纳税人
            this.funBar = this.vat
          }
        } else if (typename == 'finance') {
          if (this.$store.state.companyCurrent.gscompany.tax_type === 2) {
            this.funBar = this.financePer
          } else {
            this.funBar = this.finance
          }
          
        } else if (typename == 'season') {
          this.funBar = this.season
        } else if (typename == 'year') {
          this.funBar = this.year
        } else if (typename == 'yhs') {
          this.funBar = this.yhs
        }

