        sessionStorage.setItem('typename', JSON.stringify(this.funBar))  //添加到sessionStorage
      } else {
        //浏览器内提取功能列表
        this.funBar = JSON.parse(sessionStorage.getItem('typename'))   //从sessionStorage中读取状态
