export default {
  methods: {
    // 未开发
    useless() {
      this.$Message.error('此功能未开发：系统业务暂未涉及')
    },
    // error message
    eg(msg) {
      this.$Message.error(msg)
      // this.$Notice.error({
      //   title: '抱歉，操作失败',
      //   duration: 2,
      //   desc: msg
      // })
    },
    success(nodesc) {
      this.$Notice.success({
        title: '保存成功',
        desc: nodesc
      })
    },
    inputChangeEvent() {
      let that = this
      J('input').blur(function () {
        that.fn()
        let value = J(this).val()
        let tex = J(this).attr('bt')

        if (tex != 'text') {
          if (tex != 'zheng') {
            if (!J.isNumeric(value)) {
              if (value != '----') {
                J(this).val('0.00')
              }
            } else if (value != 0) {
              J(this).val(parseFloat(value).toFixed(2))
            } else {
              J(this).val('0.00')
            }
          } else {
            J(this).val(value * 1)
          }
        }
      })
    },
    inputClickEvent() {
      let that = this
      J('input').click(function () {
        let value = J(this).val()
        let ID = J(this).attr('id')
        let inputclass = J(this).attr('class')

        if (inputclass != 'tiaochu') {
          if (value == 0 || ID == 'zzsjmssbmxb_ycA_tou' || ID == 'zzsjmssbmxb_ycB_tou') {
            J(this).val('')
          } else if (!J.isNumeric(value)) {
            if (value != '----') {
              J(this).val('0.00')
            }
          } else {
            J(this).val(value * 1)
          }
        }
      })
      this.fn()
    },
    //数据清除
    dataRestore() {
      //判断input的情况
      for (let i = 0; i < J('form input').length; i++) {
        let bt = J('form input').eq(i).attr('bt')
        let name = J('form input').eq(i).attr('name')
        let type = J('form input').eq(i).attr('type')
        let tiaochu = J('form input').eq(i).attr('class')
        if (tiaochu != 'tiaochu') {
          if (bt != 'text') {
            //单选多选按钮状态清除
            if (type == 'radio') {
              if (J('form input').eq(i).attr('disabled')) {

              } else {
                J('form input').eq(i).prop('checked', false)
              }

            } else if (name != undefined) {
              J('form input').eq(i).val('0.00')
            } else {
              J('form input').eq(i).val('----')
            }
          } else {
            J('form input').eq(i).val('')
          }
        }
      }
      //获取预设数据
      this.getSetting()
      //判断是否存在表间取值关系
      if (this.formrevise != undefined) {
        this.formrevise()
      }
      //执行运算
      this.fn()
    },
    plus(arr) {
      let sum = 0
      arr.map(v => {
        sum += parseFloat(v)
      })
      return sum
    },
    saveForm() {
      let data = J('form').serializeArray()
      if (this.name == 36) {
        this.$store.commit('saveConfirm', { name: 'CJS', data: true })
      }
      this.$store.commit('saveData', { name: this.name, data: data })
      if (this.$store.state.tableData[this.name]) {
        this.success()
      } else {
        this.success()
      }
    },
    // 表单数据保存
    savePresetForm() {
      let data = J('form').serializeArray()
      if (this.name == 36) {
        this.$store.commit('savePresetConfirm', { name: 'CJS', data: true })
      }
      this.$store.commit('savePresetData', { name: this.name, data: data })

      if (this.$store.state.preset.presetData[this.name]) {
        this.success()
      } else {
        this.success()
      }
    },
    // 企业基础信息设置
    saveCompanyInfo() {
      let data = J('.companyInfo').serializeArray()

      if (data[0].value) {
        this.$store.commit('saveCompany', { name: data[0].value, data: data })
        //  检测数据添加是否成功
        if (this.$store.state.preset.companyInfo.company[data[0].value]) {
          this.success()
        } else {
          this.success()
        }
      } else {
        this.$Message.error('企业编码错误！')
      }
    }
  }
}



