<script>
  import Cookies from 'js-cookie'

  export default {
    data() {
      return {
        formValidate: {
          username: '',
          password: '',
          yzm: ''
        }
      }
    },
    mounted: function () {
      J('.fanhui').on('click', function () {
        J('.noA').css('display', 'block')
        J('.noB').css('display', 'none')
      })
      J('.denglu').on('click', function () {
        J('.noA').css('display', 'none')
        J('.noB').css('display', 'block')
      })
    },
    methods: {
      //登录验证
      handleSubmit() {
        if (this.formValidate.username === '') {
          this.$Message.error('纳税人识别号不得为空！')
          return false
        }
        if (this.formValidate.password === '') {
          this.$Message.error('请输入登录密码！')
          return false
        }
        if (this.formValidate.yzm === '') {
          this.$Message.error('请输入验证码！')
          return false
        }
        if (this.formValidate.yzm.toUpperCase() !== 'UUFE') {
          this.$Message.error('验证码填写错误！')
          return false
        }
        if (this.formValidate.username === this.$store.state.companyCurrent.gscompany.firm_code &&
          this.formValidate.password === '123456' &&
          this.formValidate.yzm.toUpperCase() === 'UUFE') {
          Cookies.set('islogin', 1, { SameSite: 'Strict' })
          this.$router.push({ path: '/home' })
        } else {
          this.$Message.error('纳税人识别号或密码填写错误！')
          return false
        }
      },
      //退出系统
      handleReset(name) {
        this.$refs[name].resetFields()
      },
      post(url, data, fn) {         // datat应为'a=a1&b=b1'这种字符串格式，在jq里如果data为对象会自动将对象转成这种字符串格式
        let obj = new XMLHttpRequest()
        obj.open('POST', url, true)
        obj.onreadystatechange = function () {
          if (+obj.readyState === 4 && (+obj.status === 200 || +obj.status === 304 || +obj.status === 0)) {  // 304未修改
            fn.call(this, JSON.parse(obj.responseText))
          }
        }
        obj.send(data)
      }
    }
  }
</script>
